import { useSelector } from "react-redux";
import { useSearchParams } from "react-router-dom";
import InstituteNavbarConfig from "../InstituteMember/Config/InstituteNavbarConfig";
import InstituteMemberConfig from "../InstituteMember/Config/InstituteMemberConfig";
import GoBack from "../Shared/StaffStudentProfile/GoBack";
import { Suspense, useEffect } from "react";
import QvipleLoader from "../Loader/QvipleLoader";
import SiteHomeOpener from "./HomeOpener/SiteHomeOpener";
import SiteTestimonial from "./SiteTestimonial/SiteTestimonial";
import IsoCertificate from "./IsoCertificate/IsoCertificate";
import LandingControl from "./LandingControl/LandingControl";
import HomeFounders from "./HomeFounders/HomeFounders";
import Accreditation from "./Accreditation/Accreditation";
import ImageGallary from "./ImageGallary/ImageGallary";
import SiteAffiliation from "./SiteAffiliation/SiteAffiliation";
import SociatyTrust from "./SociatyTrust/SociatyTrust";
import { useOneInstituteDashboard } from "../../hooks/dashboard/dashboard-api";

const InstituteOtherSiteConfig = () => {
  const [searchParams] = useSearchParams();
  const instituteId = useSelector((state) => state.idChange.id);

  const { oneInstituteDashboard, oneInstituteDashboardRefetch } =
    useOneInstituteDashboard({
      id: instituteId,
      skip: !instituteId,
    });
  useEffect(() => {
    if (instituteId) oneInstituteDashboardRefetch();
  }, [instituteId, oneInstituteDashboardRefetch]);

  return (
    <InstituteNavbarConfig>
      <InstituteMemberConfig>
        <section
          style={{
            width: "100%",
          }}
        >
          <GoBack
            customStyle={{
              width: "100%",
              marginTop: "0",
              marginBottom: "1rem",
            }}
          />
          <Suspense fallback={<QvipleLoader />}>
            {searchParams.get("type") === "home-opener" ? (
              <SiteHomeOpener instituteId={instituteId} />
            ) : searchParams.get("type") === "testimonial" ? (
              <SiteTestimonial instituteId={instituteId} />
            ) : searchParams.get("type") === "iso" ? (
              <IsoCertificate
                instituteId={instituteId}
                // lcid={oneInstituteDashboard?.institute?.lg}
                lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "landing-control" ? (
              <LandingControl
                instituteId={instituteId}
                // lcid={oneInstituteDashboard?.institute?.lg}
                lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "founder" ? (
              <HomeFounders
                instituteId={instituteId}
                // lcid={oneInstituteDashboard?.institute?.lg}
                lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "accreditation" ? (
              <Accreditation
                instituteId={instituteId}
                // lcid={oneInstituteDashboard?.institute?.lg}
                lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "gallary" ? (
              <ImageGallary
                instituteId={instituteId}
                // lcid={oneInstituteDashboard?.institute?.lg}
                lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "affiliation" ? (
              <SiteAffiliation
                instituteId={instituteId}
                // lcid={oneInstituteDashboard?.institute?.lg}
                lcid="661333967aa7af061e0f0aaa"
              />
            ) : searchParams.get("type") === "sociaty-trust" ? (
              <SociatyTrust
                instituteId={instituteId}
                // lcid={oneInstituteDashboard?.institute?.lg}
                lcid="661333967aa7af061e0f0aaa"
              />
            ) : null}
          </Suspense>
        </section>
      </InstituteMemberConfig>
    </InstituteNavbarConfig>
  );
};

export default InstituteOtherSiteConfig;
